

.tabsWrapper {
    position: relative;
    box-shadow: 0 0 1px 0 rgba(0,0,0,.16), 0 2px 2px 0 rgba(0,0,0,.05), 0 3px 1px -2px rgba(0,0,0,.06), 0 1px 5px 0 rgba(0,0,0,.1);
    border-radius: 4px;
    box-sizing: border-box;
    display: block;
    margin-bottom: 24px;
    transition: box-shadow .1s linear;
    background: #fff;
}

.tabsWrapper .tabs {
    -moz-border-bottom-left-radius: 4px;
    border-bottom-left-radius: 4px;
    -moz-border-bottom-right-radius: 4px;
    border-bottom-right-radius: 4px;
}

.tabs {
    background-color: #fff;
    -moz-border-top-right-radius: 4px;
    border-top-right-radius: 4px;
    -moz-border-top-left-radius: 4px;
    border-top-left-radius: 4px;
}

.tabsWrapper .headers {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.tabs .headers {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #dcdddd;
}

.tabsWrapper .headers li {
    display: table-cell;
    text-align: center;
    cursor: pointer;
}

.tabs .headers>div, .tabs .headers>li {
    display: flex;
    position: relative;
    height: 47px;
}

.tabsWrapper .tabs .content {
    visibility: visible;
    height: inherit;
}

.tabs .content {
    background-color: #fff;
    height: 0;
    padding: 0;
    visibility: hidden;
    -moz-border-bottom-left-radius: 4px;
    border-bottom-left-radius: 4px;
    -moz-border-bottom-right-radius: 4px;
    border-bottom-right-radius: 4px;
}


.tabs .headers>li a {
    position: relative;
    padding: 8px 16px 7px;
    font: 400 15px/20px Source Sans Pro,sans-serif;
    font-weight: 600;
    background: #fff;
    color: #747678;
    outline: 0;
    display: flex;
    align-items: center;
    white-space: nowrap;
    transition: background-color .1s ease;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.tabs .headers>li:first-child a {
    border-top-left-radius: 4px;
}

.tabs .headers>li a:hover {
    background: #eee;
}

.tabs .headers>li.active a:hover {
    background: #deeafa;
}

.tabs .headers>li.active a {
    background: #eff5fd;
    color: #317ae2;
}

.tabs .headers>li.active a:after {
    content: "";
    display: block;
    position: absolute;
    background: #317ae2;
    height: 2px;
    bottom: -1px;
    width: 100%;
    left: 0;
    z-index: 1;
}

.tabs .content .tabContent {
    min-height: 100px;
}



.tabContent {
    padding: 24px;
    font: 400 15px/20px Source Sans Pro,sans-serif;
}

.tabContent .listWrapper > div {
    display: flex;
    flex-direction: row;
    -webkit-box-align: center;
    align-items: center;
}

.tabContent .listWrapper .listHeader {
    border-bottom: 2px solid #d4d7dc;
}

.tabContent .listWrapper .listRow {
    border-bottom: 1px #e5e5e5 dotted;
    line-height: 16px;
    color: rgb(64, 67, 70);
    padding: 8px;
}

.tabContent .listWrapper .listHeader {
    text-transform: uppercase;
    color: rgba(38, 41, 44, 0.64);
    font-size: 13px;
    font-weight: 600;
    min-height: 16px;
    line-height: 16px;
    padding: 4px 8px;
}
.tabContent .listWrapper .listRow .listCell, .tabContent .listWrapper .listHeader .listHeaderCell {
    flex-grow: 2;
    text-align: left;
    vertical-align: middle;
    flex-basis: 0px;
    margin-right: 40px;
}

.tabContent .listWrapper .listRow .listCell .rowType {
    font-size: 11px;
    font-weight: 600;
    margin-right: 11px;
    display: inline-block;
    line-height: 32px;
    vertical-align: middle;
    text-align: center;
    width: 60px;
    height: 32px;
    background-color: rgba(38, 41, 44, 0.32);
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    border-radius: 3px;
}

.tabContent .listWrapper .listRow:hover {
    background-color: rgb(239, 245, 253);
}

