.grid {
    margin-top: 20px;
}

table {
    display: table;
    border-collapse: collapse;
    border-spacing: 0;
}

.gridContent__table {
    background: #fff;
    width: 100%;
    overflow-y: hidden;
    border-collapse: separate;
}

.gridHeader__table {
    border-bottom: none;
    border-top: none;
}

/**
.gridHeader__row {
    display: flex;
    flex-direction: row;
    
}

.gridHeader__row>* {
    flex: 0 1 auto;
}
**/

.gridHeader__cell {
    border-top: 1px solid #b9babb;
    border-bottom: 1px solid #b9babb;
    background: #fff;
    height: 22px;
    text-align: left;
    padding: 6px 8px;
    font: 600 15px/20px Source Sans Pro,sans-serif;
    cursor: pointer;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-right: 1px solid #b9babb;
}


.gridHeader__cell .headerContentItem:after {
    content: "";
    display: inline-block;
    vertical-align: top;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
    background-position: 100% 0;
    background-size: 16px;
    margin-left: 3px;
}

.gridHeader__cell.ascending .headerContentItem:after {
    background-image: url(../images/sm-triangle-up.svg);
}
.gridHeader__cell.descending .headerContentItem:after {
    background-image: url(../images/sm-triangle-down.svg);
}

.gridHeader__cell.ascending, .gridHeader__cell.descending {
    background: #e5e5e5;
}

thead {
    
    vertical-align: middle;
    border-color: inherit;
}




.grid__content {
    top: 34px;
    bottom: 0;
    right: 0;
    left: 0;
    overflow-x: hidden;
    overflow-y: auto;
    min-width: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    z-index: 1;
    
}

.grid__content tbody {
    display: block;
}

.gridRow {
    /** display: flex;
    flex-direction: row; **/
    box-sizing: border-box;
    background-color: #fff;
    min-height: 33px;
    height: 33px;
    max-height: 33px;
}

.gridRow:hover {
    z-index: 5;
    overflow-y: visible;
    background-color: #f7f7f7;
}

.gridRow__cell {
    position: relative;
    font: 400 15px/20px Source Sans Pro,sans-serif;
    height: 20px;
    overflow: hidden;
    padding: 6px 8px;
    border-bottom: 1px solid #e5e5e5;
    border-right: 1px solid #e5e5e5;
    vertical-align: middle;
}

.gridRow__cell a {
    text-decoration: none;
    color: #26292c;
}

.gridRow--hover {
    z-index: 5;
    overflow-y: visible;
    background-color: #f7f7f7
}

.gridRow__cell a:hover {
    text-decoration: underline;
}

