
.detailViewWrapper {
    height: 100%;
    overflow: auto;
}

.detailView {
    position: relative;
    max-width: 1440px;
    margin: 0 auto;
    height: 100%;
}

.detailViewWrapper .infoBlock {
    padding: 24px;
    background-color: #fff;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
}

.detailViewWrapper .infoBlock {
    border: 1px solid #e5e5e5;
    margin: 24px 24px 0;
    position: relative;
    transition: margin-top .2s;
    border-radius: 4px;
}

.detailViewWrapper .infoBlock .content {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.detailViewWrapper .infoBlock .content {
    width: 100%;
    height: 100%;
}

.detailViewWrapper .infoBlock .content .contact.icon.pictureUpload {
    cursor: pointer;
}

.detailViewWrapper .infoBlock .contact.icon {
    margin-right: 16px;
}

.detailViewWrapper .infoBlock .content .contact.icon, 
.detailViewWrapper .infoBlock .content .contact.icon .contactPicture, 
.detailViewWrapper .infoBlock .content .contact.icon .mask {
    display: block;
    width: 40px;
    height: 40px;
}

.detailViewWrapper .infoBlock .content .contact.icon .mask {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    background-color: #fff;
    border-radius: 20px;
    box-shadow: inset 0 0 0 1px #b9babb;
}

.detailViewWrapper .infoBlock .content .contact.icon {
    float: left;
    margin-right: 16px;
    position: relative;
}

.cui4-icon--blue {
    fill: #317ae2;
}

.cui4-icon {
    display: inline-flex;
    flex: 0 0 auto;
    box-sizing: border-box;
    width: 24px;
    height: 24px;
    fill: #404346;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.detailViewWrapper .infoBlock .content .contact.icon.pictureUpload:after {
    content: "";
    width: 0;
    height: 0;
    position: absolute;
    bottom: 2px;
    right: 2px;
    border-color: transparent transparent #b9babb;
    border-style: solid;
    border-width: 0 0 5px 5px;
}

.detailViewWrapper .infoBlock h1 {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}

/* MainBlock */

.detailViewWrapper .mainBlock {
    display: flex;
    position: relative;
    width: 100%;
}

.detailViewWrapper .sidebar {
    flex: 0 0 auto;
    width: 480px;
    margin: 24px;
}

.detailViewWrapper .sidebar .widget {
    display: block;
    width: 100%;
    margin-bottom: 8px;
    background-color: #fff;
    padding: 16px;
    border: 1px solid #e5e5e5;
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
    border-radius: 4px;
}

.detailViewWrapper .sidebar .widget .fieldsView {
    padding: 0;
}

.detailViewWrapper .sidebar .widget .fieldsView .columnTitle {
    padding: 16px 0;
    border-bottom: 1px solid #e5e5e5;
    margin: 0 16px;
}

.detailViewWrapper .sidebar .widget .columnTitle {
    height: 24px;
    padding-bottom: 8px;
    position: relative;
}

.detailViewWrapper .sidebar .columnTitle {
    display: flex;
    justify-content: space-between;
}

.detailViewWrapper .sidebar .widget .columnTitle .columnItem {
    display: inline-block;
    font-size: 13px;
    margin-top: 2px;
    color: #721ea9;
    font-weight: 600;
    text-transform: uppercase;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.detailViewWrapper .fieldsView .fieldsList>div.item {
    position: relative;
    padding: 8px 16px;
}

.detailViewWrapper .fieldsView .fieldsList> div.item:after {
    content: "";
    display: table;
    clear: both;
}

.detailViewWrapper .fieldsView .fieldsList > div.item .labelWrap, .detailViewWrapper .fieldsView .fieldsList > div.item .labelWrap span.label {
    font: 400 13px/16px Source Sans Pro,sans-serif;
    font-weight: 600;
    color: rgba(38,41,44,.64);
    text-align: right;
    word-wrap: break-word;
    margin: 2px 0;
}

.detailViewWrapper .fieldsView .fieldsList > div.item .labelWrap {
    display: inline-block;
    float: left;
    width: 128px;
}



.detailViewWrapper .fieldsView .fieldsList > div.item .valueWrap .phoneGroup {
    display: flex;
    margin-bottom: 0;
    right: 8px;
    bottom: 2px;
}

.detailViewWrapper .fieldsView .fieldsList > div.item .valueWrap {
    display: inline-block;
    align-items: center;
    float: right;
    position: relative;
    width: calc(100% - 144px);
    line-height: 1.3;
    word-wrap: break-word;
}

.detailViewWrapper .fieldsView .fieldsList > div.item .labelWrap, 
.detailViewWrapper .fieldsView .fieldsList > div.item .labelWrap span.label {
    font: 400 13px/16px Source Sans Pro,sans-serif;
    font-weight: 600;
    color: rgba(38,41,44,.64);
    text-align: right;
    word-wrap: break-word;
    margin: 2px 0;
}

.detailViewWrapper .fieldsView .fieldsList > div.item .valueWrap span.value {
    padding: 1px 7px;
    max-width: 240px;
    text-align: left;
    background-color:transparent;
    font: 400 15px/20px Source Sans Pro,sans-serif;
    color: #317ae2;
    border: 1px solid transparent;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
    -webkit-user-select: text;
    -ms-user-select: text;
    user-select: text;
}


/* Overview */

.barChart {
    width: 100%;
    padding: 11px 0 16px;
}

.barChart .chartTitle {
    padding-bottom: 7px;
    padding-left: 1px;
    font: 400 15px/20px Source Sans Pro,sans-serif;
    color: #26292c;
}

.barChart .bars {
    width: 100%;
    height: 16px;
    overflow: hidden;
    border-radius: 15px;
    position: relative;
    top: 0;
    left: 0;
    padding: 0;
    margin-bottom: 12px;
}

.barChart .bars .bar {
    -ms-box-sizing: border-box;
    -o-box-sizing: border-box;
    box-sizing: border-box;
    float: left;
    display: block;
    width: 100%;
    height: 16px;
    position: relative;
}

.barChart.orange .bar:nth-child(5n-4) .barContent {
    background-color: #fc0;
}
.barChart .bars .bar:last-child .barContent {
    margin-right: 0;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}
.barChart .bars .bar:first-child .barContent {
    margin-left: 0;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}
.barChart .bars .bar .barContent {
    position: absolute;
    background-color: #404346;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: 0 1px;
    transition: top .1s,left .1s,right .1s,bottom .1s;
}

.barChart .legend {
    position: relative;
    font: 400 13px/16px Source Sans Pro,sans-serif;
    color: #747678;
    
}

.barChart div.legend:after {
    content: "";
    display: table;
    clear: both;
}


.barChart .legend .labelWrap {
    display: block;
    float: left;
    width: 128px;
}

.barChart .legend .valueWrap {
    display: block;
    float: right;
}









.detailViewWrapper .mainBlock>.content {
    flex: 1;
    min-width: 0;
    margin: 24px 24px 24px 0;
}