body nav {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    transition: top .2s;
    height: 48px;
    z-index: 2000;
    background: #404346;
    box-shadow: inset 0 -1px 0 rgba(0,0,0,.7);
}

nav .menuWrapper {
    height: 48px;
    overflow: hidden;
}

nav .menuWrapper #mainmenu {
    user-select: none;
    color: #fff;
    margin: 0;
    padding: 0;
    white-space: nowrap;
    overflow: hidden;
    display: block;
    float: left;
    z-index: 2000;
    width: 100%;
}

#dashboard section ul li:after, #dashboard section ul li:before, .clearfix:after, .clearfix:before {
    content: " ";
    display: block;
    height: 0;
    overflow: hidden;
}

nav .menuWrapper #mainmenu menu, body nav .menuWrapper #mainmenu menu li {
    margin: 0;
    padding: 0;
    display: block;
    white-space: nowrap;
}


nav .menuWrapper #mainmenu menu li {
    position: relative;
    float: left;
    list-style: none;
}

nav .menuWrapper #mainmenu menu li a {
    color: #b9babb;
    display: block;
    overflow: hidden;
    font-weight: 400;
    padding: 12px;
    font-size: 15px!important;
    line-height: 20px;
    white-space: nowrap;
    text-overflow: ellipsis;
    text-decoration: none;
}

nav .menuWrapper #mainmenu menu li a .label {
    margin-left: 8px;
    padding: 2px 0;
    font: 400 15px/20px Source Sans Pro,sans-serif;
    
}

body nav .logo_container {
    width: 122px;
    float: left;
}

#global-search {
    display: block;
    float: left;
    padding: 8px;
    transition: background-color .15s ease-in;
    background-color: transparent;
    position: relative;
}