.user-profile-corner {
    user-select: none;
    float: right;
    display: flex;
}

.user-profile-corner a {
    float: right;
    display: block;
    padding: 8px 14px 8px 12px;
    color: #fff;
    line-height: 16px;
    text-decoration: none;
}

.user-profile-corner .profilePlaceholder, .user-profile-corner img {
    float: left;
    width: 32px;
    height: 32px;
    overflow: hidden;
    border-radius: 16px;
}

.user-profile-corner .userInfo {
    margin: 0 8px;
    float: left;
}

.user-profile-corner .userInfo span.name {
    display: block;
    white-space: nowrap;
}

.user-profile-corner .userInfo span.company {
    display: block;
    font-size: 11px;
    color: #b9babb;
}

.user-profile-corner a.active {
    background-color: #fff;
    color: #26292c;
}

.user-profile-corner a.active:active {
    background-color: #e5e5e5!important;
    opacity: 1;
    color: #26292c!important;
}


.dropMenu {
    right: 0;
    top: 48px;
    height: auto;
    position: absolute;
    display: block;
    opacity: 100;
    max-width: 350px;
    min-width: 50px;
    background: #fff;
    font-size: .9em;
    background-clip: padding-box;
    overflow-x: hidden;
    z-index: 3000;
    box-shadow: 0 4px 8px rgba(38,41,44,.16);
    border-radius: 0 2px 2px 2px;
}

.dropMenu .headerDropMenu {
    padding: 8px 0;
}

.dropMenu ul {
    width: 100%;
    list-style-type: none;
}

.dropMenu ul li {
    width: 100%;
    position: relative;
}

.dropMenu .headerDropMenu .dropMenuSeparator {
    margin: 8px 0;
}

.dropMenu ul li.dropMenuSeparator {
    height: 1px;
    margin: 10px 0;
    background-color: #e5e5e5;
}

.dropMenu .headerDropMenu li a {
    padding: 6px 48px 6px 16px;
}

.dropMenu .headerDropMenu li a, .dropMenu .headerDropMenu li span {
    display: flex;
    align-items: center;
}
.dropMenu ul li a, .dropMenu ul li span.action {
    text-decoration: none;
    display: flex;
    align-items: center;
    padding: 6px 15px;
    cursor: pointer;
    color: #26292c;
    font-size: 15px;
    line-height: 16px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.dropMenu .headerDropMenu li a, .dropMenu .headerDropMenu li span {
    display: flex;
    align-items: center;
}

.dropMenu ul li a:hover, .dropMenu ul li span.action:hover {
    text-decoration: none;
    background-color: #317ae2;
    color: #fff;
}

